  background: -webkit-gradient(linear, left top, right top, from(rgba(21, 220, 255, 0.2)), color-stop(51%, rgba(255, 89, 136, 0.2)), to(rgba(255, 192, 67, 0.2)));
  background: linear-gradient(to right, rgba(21, 220, 255, 0.2) 0%, rgba(255, 89, 136, 0.2) 51%, rgba(255, 192, 67, 0.2) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#15dcff', endColorstr='#ffc043', GradientType=1);
  z-index: 99;
}
.btnGradient:hover {
  color: #ffffff;
  border-color: rgba(255, 255, 255, 0.2);
  -webkit-box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
          box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}
.btnGradient:hover:after {
  left: 120%;
}
.btnGradient:focus {
  color: #ffffff;
  border-color: rgba(255, 255, 255, 0.2);
  -webkit-box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
          box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
