.btnGradient:after {
  background: #ffffff;
  content: "";
  height: 155px;
  left: -75px;
  opacity: .2;
  position: absolute;
  top: -50px;
  -webkit-transform: rotate(35deg);
  transform: rotate(35deg);
  -webkit-transition: all 1550ms cubic-bezier(0.19, 1, 0.22, 1);
  transition: all 1550ms cubic-bezier(0.19, 1, 0.22, 1);
