.tooltip .tooltip-inner {
  border-radius: 0;
  overflow: hidden;
  position: relative;
}
.tooltip.bs-tether-element-attached-bottom .tooltip-inner {
  background: rgba(21, 220, 255, 0.2);
  background: -webkit-gradient(left top, right top, color-stop(0%, rgba(21, 220, 255, 0.2)), color-stop(51%, rgba(255, 89, 136, 0.2)), color-stop(100%, rgba(255, 192, 67, 0.2)));
  background: -webkit-gradient(linear, left top, right top, from(rgba(21, 220, 255, 0.2)), color-stop(51%, rgba(255, 89, 136, 0.2)), to(rgba(255, 192, 67, 0.2)));
  background: linear-gradient(to right, rgba(21, 220, 255, 0.2) 0%, rgba(255, 89, 136, 0.2) 51%, rgba(255, 192, 67, 0.2) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#15dcff', endColorstr='#ffc043', GradientType=1);
}
.tooltip.bs-tether-element-attached-bottom .tooltip-inner::after {
  display: none;
}
.tooltip.bs-tether-element-attached-bottom .tooltip-inner::before {
  display: none;
}
.tooltip.bs-tether-element-attached-top .tooltip-inner::after {
  display: none;
}
.tooltip.bs-tether-element-attached-top .tooltip-inner::before {
  display: none;
}
.headline {
  margin-top: 10px;
  color: #ffffff;
}
.headline .animated-words {
  display: inline-block;
  position: relative;
  text-align: left;
}
.headline .animated-words b {
  display: inline-block;
  position: absolute;
  white-space: nowrap;
  left: 0;
  top: 0;
}
.headline .animated-words b.is-visible {
  position: relative;
}
.headline.loading-bar span {
  display: inline-block;
  padding: .2em 0;
}
.headline.loading-bar .animated-words {
  overflow: hidden;
  vertical-align: top;
}
.headline.loading-bar .animated-words::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  height: 4px;
  width: 0;
  background: #15dcff;
  background: -webkit-gradient(left top, right top, color-stop(0%, #15dcff), color-stop(51%, #ff5988), color-stop(100%, #ffc043));
  background: -webkit-gradient(linear, left top, right top, from(#15dcff), color-stop(51%, #ff5988), to(#ffc043));
  background: linear-gradient(to right, #15dcff 0%, #ff5988 51%, #ffc043 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#15dcff', endColorstr='#ffc043', GradientType=1);
  z-index: 2;
  -webkit-transition: width 0.3s -0.1s;
  transition: width 0.3s -0.1s;
}
.headline.loading-bar .animated-words b {
  opacity: 0;
}
.headline.loading-bar .animated-words b.is-visible {
  opacity: 1;
}
.headline.loading-bar .animated-words.is-loading::after {
  width: 100%;
  -webkit-transition: width 3s;
  transition: width 3s;
}
.headline.loading-bar b {
  top: .2em;
  opacity: 0;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
}
.headline.loading-bar b.is-visible {
  opacity: 1;
  top: 0;
}
