label.dropdown {
  position: relative;
}
label.dropdown select {
  padding: 10px 42px 10px 10px;
  background: #f8f8f8;
  color: #444;
  border: 1px solid #aaa;
  border-radius: 0;
  display: inline-block;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  cursor: pointer;
  outline: none;
}
label.dropdown select:-moz-focusring {
  color: transparent;
  text-shadow: 0 0 0 #444;
}
label.dropdown select::-ms-expand {
  display: none;
}
label.dropdown:before {
  content: '';
  right: 5px;
  top: -7px;
  width: 30px;
  height: 33px;
  background: #f8f8f8;
  position: absolute;
  pointer-events: none;
  display: block;
}
label.dropdown:after {
  content: '>';
  font: 16px Consolas, monospace;
  color: #444;
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
  right: 2px;
  top: -3px;
  border-bottom: 1px solid #aaa;
  position: absolute;
  pointer-events: none;
  width: 34px;
  padding: 0 0 5px 0;
  text-indent: 14px;
}
